import React from 'react'
import { Card, CardMedia, CardTitle, CardText, CardActions } from 'react-toolbox/lib/card';
import SVG from 'react-inlinesvg';
import {Button} from 'react-toolbox/lib/button';
import svg from './legend.svg'
import img from './legend.png'
import votes from './votes.svg'
import shapes from './shapes.svg'
import Table from './Table';
import Icon from './Icon'
import styles from './style.pcss';
import radiusSize from './radius-size.svg'
import colorimg from './colorimg.png';

const tableValues = [{
  right: "Top 10%",
  left: <Icon className={styles.ten} value="lens" />,
},{
  right: "Top 20%",
  left: <Icon className={styles.twenty} value="lens" />,
},{
  right: "Top 50%",
  left: <Icon className={styles.fifty} value="lens" />,
},{
  right: "Top 75%",
  left: <Icon className={styles.seventy} value="lens" />,
},{
  right: "Top 90%",
  left: <Icon className={styles.ninety} value="lens" />,
},{
  right: 'Connections',
  left: <SVG className={styles.svg} src={radiusSize} />
},]

export default class extends React.PureComponent {
  state = {
    showTable: true
  }
  toggleShowTable = () => {
    this.setState({
      showTable: !this.state.showTable,
    })
  }
  render(){
    return <div className={styles.container}>
      <Card>
        {
          this.state.showTable && <CardText>
              <div><SVG src={votes} /></div>
              <div><img src={colorimg} /></div>
              <div><SVG src={shapes} /></div>
            {
              // <img src={img} />
            }            
        </CardText>
        }
        <CardActions>
          <Button onClick={this.toggleShowTable} label={`${this.state.showTable ? "Hide" : "Show"} Legend`} />
        </CardActions>
      </Card>
    </div>
  }
}


